/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Merriweather+Sans");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  position: relative;
  min-height: 100%; }

body {
  background-image: linear-gradient(to right, #cf6a87, #e77f67);
  font-family: 'Merriweather Sans', sans-serif; }
  body #root #wrap .weather-info {
    margin-top: 30px;
    margin-bottom: 150px; }
    body #root #wrap .weather-info .container .row:nth-child(1) .mx-auto .headingTitle {
      text-align: center;
      padding-bottom: 20px; }
      body #root #wrap .weather-info .container .row:nth-child(1) .mx-auto .headingTitle h1 {
        color: #dcdde1;
        font-size: 40px; }
        body #root #wrap .weather-info .container .row:nth-child(1) .mx-auto .headingTitle h1 svg {
          color: #dcdde1; }
    body #root #wrap .weather-info .container .row:nth-child(1) .mx-auto .card {
      box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.5);
      background-color: #353b48;
      border-radius: 0; }
      body #root #wrap .weather-info .container .row:nth-child(1) .mx-auto .card .card-body .form .col-md-10 .form-group .form-control {
        border-radius: 0;
        background-color: rgba(0, 0, 0, 0);
        border: none;
        border-bottom: 2px solid #cf6a87;
        transition: all .3s;
        font-size: 20px;
        color: #7f8fa6; }
        body #root #wrap .weather-info .container .row:nth-child(1) .mx-auto .card .card-body .form .col-md-10 .form-group .form-control:focus {
          border-bottom: 2px solid #dcdde1; }
      body #root #wrap .weather-info .container .row:nth-child(1) .mx-auto .card .card-body .form .col-md-2 .form-group button {
        color: #353b48;
        font-weight: bold;
        border-radius: 0;
        background-color: #dcdde1;
        transition: all .3s; }
        body #root #wrap .weather-info .container .row:nth-child(1) .mx-auto .card .card-body .form .col-md-2 .form-group button:hover {
          opacity: .7; }
    body #root #wrap .weather-info .container .row:nth-child(2) .mx-auto .card {
      box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.5);
      background-color: #353b48;
      border-radius: 0; }
      body #root #wrap .weather-info .container .row:nth-child(2) .mx-auto .card .card-body .card-title {
        color: #dcdde1;
        padding: 10px; }
  body #root #wrap .footer {
    position: absolute;
    bottom: 0;
    background-color: #353b48;
    width: 100%;
    line-height: 20px; }
    body #root #wrap .footer .container-fluid .row .col-lg-6:nth-child(1) .card {
      text-align: center;
      border: none;
      background-color: #353b48; }
      body #root #wrap .footer .container-fluid .row .col-lg-6:nth-child(1) .card .card-body i {
        color: #dcdde1;
        font-size: 17px; }
      body #root #wrap .footer .container-fluid .row .col-lg-6:nth-child(1) .card .card-body a {
        color: #dcdde1; }
    body #root #wrap .footer .container-fluid .row .col-lg-6:nth-child(2) .card {
      text-align: center;
      border: none;
      background-color: #353b48; }
      body #root #wrap .footer .container-fluid .row .col-lg-6:nth-child(2) .card .card-body i {
        color: #dcdde1;
        font-size: 17px; }
      body #root #wrap .footer .container-fluid .row .col-lg-6:nth-child(2) .card .card-body a {
        color: #dcdde1; }

@media screen and (max-width: 400px) {
  body #root #wrap .footer .container-fluid .row .col-lg-6:nth-child(2) .card {
    display: none; } }
